<template>
  <div class="tip-wx-share" @touchmove.prevent>
    <van-popup v-model="isShow">
      <div class="div-img">
        <img src="/static/images/wx_share.png"/>
      </div>
      <p>点[发送给朋友]或[分享到朋友圈]<br>分享给你的好友</p>
    </van-popup>
  </div>
</template>

<script type="text/ecmascript-6">
export default{
  name: 'tipWxShare',
  components: {},
  props: {
    show: {
      type: Boolean, default: false, required: true
    }
  },
  data () {
    return {
      isShow: false
    }
  },
  created () {
  },
  mounted () {
  },
  computed: {
  },
  methods: {
  },
  watch: {
    show (val) {
      this.isShow = val
    },
    isShow (val) {
      if (!val) {
        this.$emit('update:show', false)
      }
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .tip-wx-share{
    /deep/ .van-popup{
      background-color: transparent;
      overflow: hidden;
      top: 0;
      left: 0;
      width: 100%;
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
      .div-img{
        position: relative;
        height: 240px;
        img{
          width: 200px;
          height: 200px;
          position: absolute;
          right: 50px;
        }
      }
      p{
        color: #fff;
        font-size: 40px;
        text-align: center;
        line-height: 50px;
      }
    }
  }
</style>



