import base from './base.js'
export default {
  data () {
    return {
    }
  },
  mixins: [base],
  computed: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    /** ***调用二维码******/
    qrCode () {
      return new Promise((resolve, reject) => {
        let result = ''
        let self = this
        self.wx.ready(function () {
          self.wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              result = res.resultStr // 当needResult 为 1 时，扫码返回的结果
              self.$toast.success(result)
              resolve(result)
            }
          })
        })
      })
    },
    /** 获取扫码授权并调用扫码***/
    openQrCode () {
      return new Promise((resolve, reject) => {
        this.weChatConfig(this.getCurrentUrl(), ['scanQRCode']).then(() => {
          this.qrCode().then(res => {
            resolve(res)
          })
        })
      })
    }
  }
}



