export const strToDate = (str, fmt) => {
  let arrStr = str.split('-')
  if (fmt === 'yy-MM') {
    return new Date(arrStr[0], arrStr[1] - 1, 1)
  }
  if (fmt === 'yy-MM-dd') {
    return new Date(arrStr[0], arrStr[1] - 1, arrStr[2])
  }
  return null
}

// 一位补0: 3 -> 03
export const addZero = (num) => {
  if (num >= 0 && num < 10) {
    return '0' + num
  }
  return '' + num
}



