<template>
<div class="app-page test1">
  <page-header title="测试1" left-arrow border left-text="返回" right-text="点击" rightPath="/">
  </page-header>
  <page-content>
    <div class="clear-storege level-center vertical-center">
      <button class="clickButton" @click="clearStorege">清理缓存</button>
    </div>

    <div class="toTest level-center vertical-center">
      <button class="clickButton" @click="toTest">toTest</button>
    </div>

    <div class="qr-code level-center vertical-center">
      <button class="clickButton" @click="clickScanQrButton">扫码</button>
      <div>结果:{{scanQrRes}}</div>
    </div>
    <div class="wxPay level-center vertical-center">
      <button class="clickButton" @click="clickWxPayBut">支付</button>
      <div>{{paySuccessResStr}}</div>
    </div>
    <div class="wxPay level-center vertical-center">
      <button class="clickButton" @click="clickWxPayPopup">支付弹框测试</button>
    </div>

    <div class="share level-center vertical-center">
      <van-icon class-prefix="van-icon" name="time" />
      <van-icon class-prefix="van-icon" name="xiangqing" />
      <van-icon class-prefix="van-icon" name="shijian" />
      <van-icon class-prefix="van-icon" name="coupon" />
      <van-field v-model="inpVal" placeholder="请输入" icon="time"/>
    </div>

    <div class="x2js level-center vertical-center">
      <button class="clickButton" @click="clickJsonIcon">json2xml</button>
      <button class="clickButton" @click="clickXmlIcon">xml2json</button>
    </div>

    <div class="tipsWxShare level-center vertical-center">
      <button class="clickButton" @click="clickShare1">分享提示</button>
    </div>
  </page-content>
  <popup-pay :show.sync="popupPayData.show" :showCoupon="true" :couponTypeCode="'ACTIVITY'" :showMarketMoney="true"
             :showPayMoney="true" :payMoney="4" :orderId="-1"></popup-pay>
  <tips-wx-share :show.sync="tipsWxShare"></tips-wx-share>
</div>

</template>
<script>
import PageHeader from '@/components/page/header'
import PageContent from '@/components/page/content'
import PopupPay from '@/components/Popup/popup-pay'
import wxConfigQrCode from '@/mixins/wxConfig/qr-code.js'
import wxConfigShare from '@/mixins/wxConfig/share.js'
import wxConfigPay from '@/mixins/wxConfig/pay.js'
import TipsWxShare from '@/components/page/tips/wx-share'
import { isEmail } from '@/utils/string'
import { strToDate } from '@/utils/date'
export default {
  name: 'test1',
  components: { PageHeader, PageContent, TipsWxShare, PopupPay },
  data () {
    return {
      scanQrRes: '',
      pval: true,
      inpVal: '',
      x2jsData: {
        jsonStr: '',
        xmlStr: ''
      },
      // 支付弹框相关数据
      popupPayData: {
        show: false
      },
      // 分享提示数据
      tipsWxShare: false
    }
  },
  mixins: [wxConfigQrCode, wxConfigShare, wxConfigPay],
  computed: {
  },
  created () {
    this.testCloneDeep()
    console.log(isEmail('1032199882@qq.com'))
  },
  mounted () {
    // this.initShareWxConfigCur()
  },
  methods: {
    clearStorege () {
      window.localStorage.clear()
      window.sessionStorage.clear()
    },
    toTest () {
      this.$router.push({path: '/test'})
    },
    testCloneDeep () {
      var obj1 = { a: 1, b: { c: 1 }, d: [{ e: 1 }] }
      var obj2 = this.$lodash.cloneDeep(obj1)
      console.log(obj1 === obj2, obj1.b.c, obj2.b.c)
      obj2.b.c = 10
      console.log(obj1 === obj2, obj1.b.c, obj2.b.c)
    },
    clickJsonIcon () {
      console.log(this.$x2js.js2xml({'a': 'aa', 'b': { c: 'cc' }}))
    },
    clickXmlIcon () {
      console.log(this.$x2js.xml2js('<a>aa</a>'))
    },
    clickWxPayPopup () {
      this.popupPayData.show = true
    },
    /** jssdk方法测试***********start**************/
    clickScanQrButton () {
      this.openQrCode().then(res => {
        this.scanQrRes = res
      })
    },
    clickWxPayBut () {
      let subData = {
        courseId: 2,
        realPayMarketCoin: 22,
        couponId: 22,
        orderType: 'course'
      }
      this.startWxPay(subData)
    },
    clickShare1 () {
      let shareData = { title: '测试1页面分享', desc: '详情xxxxx', link: '/test1', imgUrl: 'https://cn.bing.com/az/hprichbg/rb/KilimanjaroMawenzi_ZH-CN7924585833_1920x1080.jpg' }
      this.showWxFunction()
      this.updateAppMessageShareData(shareData)
      this.tipsWxShare = true
    },
    // 分享相关的微信jssdk授权
    initShareWxConfigCur () {
      this.initShareWxConfig().then(() => {
        if (!this.tipsWxShare) {
          this.hideWxFunction()
        }
      })
    }
    /** jssdk方法测试***********end**************/
  },
  watch: {
    tipsWxShare: {
      handler (nval) {
        console.log('watch')
        if (!nval) {
          this.hideWxFunction()
        }
      }
    }
  }
}

</script>
<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .test1{
    .page-content{
      >div{
        margin-top: 20px;
      }
    }
    .clickButton{
      width: 150px;height: 80px;
      background-color: grey;
    }
    .qr-code{
      border: 1px solid $green22ac38;
    }
  }
</style>



