import wx from 'weixin-js-sdk'
import globalParam from '@/globalParam.js'
export default {
  data () {
    return {
      wx: wx,
      paySuccessResStr: ''
    }
  },
  computed: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    /**
     *获取当前页面url
     */
    getCurrentUrl () {
      var url1 = location.href.split('#')[0]
      var u = navigator.userAgent
      var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
      if (isiOS) {
        // "|"转码为 %7C
        url1 = url1.replace(/\|/g, '%7C')
      }
      // return encodeURIComponent(url_1);
      return url1
    },
    /** ***调用微信js_sdk******/
    weChatConfig (url, apiList) {
      return new Promise((resolve, reject) => {
        this.$get(this.$api.wxpaygetconfig, {url: url}).then(res => {
          const datas = res.data
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: globalParam.wxAppId, // 必填，公众号的唯一标识
            timestamp: datas.timestamp, // 必填，生成签名的时间戳
            nonceStr: datas.nonceStr, // 必填，生成签名的随机串
            signature: datas.signature, // 必填，签名，见附录1
            jsApiList: apiList // 必填，需*   要使用的JS接口列表，多个接口在用逗号隔开
          })
          resolve()
        })
      })
    },
    /** ***隐藏功能设置***/
    hideWxFunction (hideList) {
      wx.ready(function () {
        if (hideList) {
          wx.hideMenuItems({
            menuList: hideList
          })
        } else {
          wx.hideAllNonBaseMenuItem()
        }
      })
    },
    /** ***显示功能设置***/
    showWxFunction (showList) {
      wx.ready(function () {
        if (showList) {
          wx.showMenuItems({
            menuList: showList
          })
        } else {
          wx.showAllNonBaseMenuItem()
        }
      })
    }
  }
}



