<template>
  <div class="popup-pay">
    <van-popup class="popup-pay-index" v-model="isShow" position="bottom">
      <div class="head align-center">
        <div></div>
        <div class="title">{{title}}</div>
        <van-icon class="close-icon" name="cross" size=".45rem" @click="isShow = false" />
      </div>
      <div class="content">
        <div class="item coupon" v-if="showCoupon" @click="openCouponPopup">
          <div>{{couponType.name}}</div>
          <div v-if="coupon.chosenCoupon >= 0">{{selCouponMoneyStr}}</div>
          <div v-else-if="coupon.availableList.length <= 0">无可用</div>
          <div v-else class="align-center">您有{{coupon.availableList.length}}张可用优惠券<van-icon class="" name="arrow" size=".45rem"/></div>
        </div>
        <div class="item market-money"  v-if="showMarketMoney">
          <div>
            <span>市集币&emsp;共<strong>{{marketMoney.amount}}</strong>币,</span>
            <span>可抵<strong>{{marketMoney.amount/marketMoney.moneyToCoinRate}}</strong>元;</span>
            <span v-show="marketMoney.isUse">当前可使用<strong>{{marketMoney.usableAmount}}</strong>市集币.</span>
          </div>
          <div><van-switch v-model="marketMoney.isUse" active-color="#D8B96F"/></div>
        </div>
        <!--<div class="item balance" @click.prevent.stop="payWay.selType='1'">
          <div>余额&ensp;(￥99.00)</div>
          <van-radio name="1" v-model="payType"></van-radio>
        </div>
        <div class="item wx-pay" @click.prevent.stop="payWay.selType='2'">
          <div>微信支付</div>
          <van-radio name="2" v-model="payType"></van-radio>
        </div>-->
        <div class="item pay-money"  v-if="showPayMoney">
          <div>付款金额</div>
          <div>￥{{payMoneyThis}}</div>
        </div>
        <div class="item pay-way" @click.prevent.stop="payWay.show = true">
          <div>支付方式&emsp;({{payWay.selType === '' ? '未选择' : payWay.selType === '1' ? '余额' : '微信支付'}})</div>
          <div><van-icon name="arrow" size=".45rem"/></div>
        </div>
      </div>
      <div class="bottom align-center level-center">
        <van-button type="default" @click="clickPay">立即付款</van-button>
      </div>
    </van-popup>
    <van-popup class="popup-pay-coupon" v-model="coupon.showCouponPopup" position="bottom">
      <van-coupon-list
        :show-exchange-bar="false"
        :coupons="coupon.availableList"
        :chosen-coupon="coupon.chosenCoupon"
        :disabled-coupons="unavailableList"
        @change="onChangeCoupon"
      />
    </van-popup>
    <van-actionsheet
      v-model="payWay.show"
      :actions="payWay.actions"
      cancel-text="取消"
      @select="payWayOnSelect"
      @cancel="payWayOnCancel"
    />
  </div>
</template>

<script>
import wxPay from '@/mixins/wxConfig/pay.js'
// const coupon = {
//   available: 1,
//   discount: 88,
//   denominations: 3000,
//   originCondition: 10000,
//   reason: '',
//   value: 0,
//   name: '优惠券名称',
//   startAt: 1489104000,
//   endAt: 1514592000
// }
export default {
  name: 'index',
  props: {
    show: { type: Boolean, default: false, required: true },
    title: { type: String, default: '确认付款', required: false },
    // 支付类型code
    payTypeCode: { type: String, default: '', required: false },
    // 是否显示'优惠券'列
    showCoupon: { type: Boolean, default: true, required: false },
    // 优惠券类型code
    couponTypeCode: { type: String, default: '', required: false },
    // 是否显示'市集币'列
    showMarketMoney: { type: Boolean, default: true, required: false },
    // 是否显示'支付金额'列
    showPayMoney: { type: Boolean, default: true, required: false },
    // 支付金额
    payMoney: { type: Number, default: 0, required: false },
    // 订单ID
    orderId: { type: Number, default: -1, required: true }
  },
  mixins: [wxPay],
  data () {
    return {
      isShow: false,
      // 优惠券相关参数
      coupon: {
        showCouponPopup: false,
        // 优惠券类型
        types: [{ code: 'DISCOUNT', name: '折扣券' }, { code: 'REDUCE', name: '立减券' }, { code: 'CASH', name: '现金券' }, { code: 'COURSE', name: '课程券' }, { code: 'ACTIVITY', name: '活动券' }],
        chosenCoupon: -1,
        availableNum: 1, // 可用数量
        availableList: [], // 可用优惠券集合
        expiredAndUsedList: [], // 过期+已使用
        usedList: [], // 已使用,
        unsatisfiedList: [] // 可以使用，但是不满足当前条件(如支付金额不足以使用该优惠券)
      },
      // 市集币相关参数
      marketMoney: {
        isUse: false,
        amount: 0, // 余额
        moneyToCoinRate: null, // 兑换现金比例
        usableAmount: 0 // 可使用市集币
      },
      // 支付方式
      payWay: {
        show: false,
        balance: 0, // 余额
        selType: '', // 字符类型: '1'余额支付,2''微信支付
        actions: [
          { name: '余额不足(￥0)', code: '1', disabled: true }, { name: '微信支付', code: '2' }
        ]
      },
      // 支付金额(payMoneyThis会根据选择优惠券/市集币而变动，payMoney为初始付款金额)
      payMoneyThis: 0
    }
  },
  created () {
  },
  computed: {
    // 根据props中couponType找到优惠券分类的name
    couponType () {
      return this.coupon.types.filter(item => {
        return item.code === this.couponTypeCode
      })[0]
    },
    // 选中优惠券的金额， 在支付弹框第一行使用
    selCouponMoneyStr () {
      let result = ''
      let selIndex = this.coupon.chosenCoupon
      if (selIndex >= 0) {
        let selCoupon = this.coupon.availableList[selIndex]
        if (selCoupon.denominations > 0) {
          result = `-￥${selCoupon.denominations / 100}`
        } else if (selCoupon.discount > 0) {
          result = `${selCoupon.discount / 10}折`
        }
      }
      return result
    },
    // 选中的优惠券
    selCoupon () {
      if (this.coupon.chosenCoupon !== -1) {
        return this.coupon.availableList[this.coupon.chosenCoupon]
      } else {
        return null
      }
    },
    // 不可用优惠券(过期+已用)
    unavailableList () {
      let result = [].concat(this.coupon.expiredAndUsedList).concat(this.coupon.unsatisfiedList)
      return result
    },
    // 根据支付类型，返回下单地址
    payUrl () {
      let res = ''
      switch (this.payTypeCode) {
        case 'ACTIVITY':
          res = this.$api.activityjoin
          break
        case 'BUYCARD':
          res = this.$api.buygiftcard
          break
        case 'COURSE':
          res = this.$api.buyCourseOrder
          break
        case 'VIP':
          res = this.$api.buyvip
          break
        case 'MARKET':
          res = this.$api.marketpay
          break
        default:
          res = ''
      }
      return res
    }
  },
  methods: {
    // 支付弹框弹出时执行
    init () {
      this.getCouponList()
      // this.getUsedCouponList()
      this.getExpiredCouponList()
      this.getMarketMoneyInfo()
    },
    /** 优惠券相关方法*********start*******/
    // 打开优惠券弹框
    openCouponPopup () {
      if (this.coupon.availableList.length > 0) {
        this.coupon.showCouponPopup = true
      }
    },
    // 选择优惠券变动时
    onChangeCoupon (index) {
      this.coupon.showCouponPopup = false
      this.coupon.chosenCoupon = index
    },
    // 获取可用优惠券方法
    getCouponList () {
      if (!this.showCoupon) {
        return
      }
      this.coupon.availableList = []
      let param = { couponType: this.couponType.code }
      this.$get(this.$api.getUnusecardList, param).then(res => {
        let availableList = this.machCouponData(res.data)
        this.findUnsatisfiedFromAvailableList(availableList)
      })
    },
    // 获取已使用优惠券方法 --停用
    getUsedCouponList () {
      if (!this.showCoupon) {
        return
      }
      this.coupon.usedList = []
      let param = { couponType: this.couponType.code }
      this.$get(this.$api.gethasUsecardList, param).then(res => {
        this.coupon.usedList = this.machCouponData(res.data)
      })
    },
    // 获取过期优惠券方法
    getExpiredCouponList () {
      if (!this.showCoupon) {
        return
      }
      this.coupon.expiredAndUsedList = []
      let param = { couponType: this.couponType.code }
      this.$get(this.$api.getUsecardList, param).then(res => {
        this.coupon.expiredAndUsedList = this.machCouponData(res.data)
      })
    },
    // 将接口返回的 可用优惠券数据 进行加工
    machCouponData (data) {
      let result = data.map(item => {
        return { id: item.id,
          couponNo: item.couponNo,
          discount: item.discount * 10,
          name: item.couponType,
          denominations: item.money * 100,
          originCondition: item.minReachAmount * 100,
          startAt: new Date(item.beginDate).getTime() / 1000,
          endAt: new Date(item.endDate).getTime() / 1000,
          reason: item.couponstatus === 0 ? '' : item.couponstatus === -1 ? '已过期' : '已使用',
          value: 0}
      })
      return result
    },
    // 从可用优惠券中找到 条件不满足的优惠券，并删除
    findUnsatisfiedFromAvailableList (availableList) {
      let unsatisfiedList = []
      this.coupon.availableList = availableList.filter(item => {
        if (item.originCondition > (this.payMoney * 100)) {
          item.reason = '支付金额不足'
          unsatisfiedList.push(item)
          return false
        } else {
          return true
        }
      })
      this.coupon.unsatisfiedList = unsatisfiedList
    },
    /** 市集币-相关方法*********start*******/
    getMarketMoneyInfo () {
      if (!this.showMarketMoney && !this.showPayMoney) {
        return
      }
      this.$get(this.$api.getMoneyInfoByUser, {}).then(res => {
        this.marketMoney.amount = res.data.marketCoinVO.amount
        this.marketMoney.moneyToCoinRate = res.data.marketCoinVO.moneyToCoinRate
        this.payWay.balance = res.data.balance
        this.judgeBalanceByPayMeonyThis(this.payMoneyThis)
      })
    },
    // 根据参数传入payMeonyThis判断，余额是否足够
    judgeBalanceByPayMeonyThis (PayMeonyThis) {
      if (this.payWay.balance < PayMeonyThis) {
        this.payWay.actions[0].name = `余额不足(￥${this.payWay.balance})`
        this.payWay.actions[0].disabled = true
      } else {
        this.payWay.actions[0].name = `余额(￥${this.payWay.balance})`
        this.payWay.actions[0].disabled = false
        if ((PayMeonyThis * 1) === 0.00 || (PayMeonyThis * 1) === 0) {
          this.payWay.actions[1].disabled = true
        } else {
          this.payWay.actions[1].disabled = false
        }
      }
      this.payWay.selType = '' // 将支付方式还原
    },
    /** 计算金额-随优惠券、市集币变动-相关方法*********start*******/
    calcPayMoneyThis () {
      // 随优惠券的选择变化
      if (this.coupon.chosenCoupon !== -1) {
        let selCoupon = this.coupon.availableList[this.coupon.chosenCoupon]
        if (selCoupon.denominations > 0) {
          let diffRes = this.payMoney - selCoupon.denominations / 100
          this.payMoneyThis = diffRes >= 0 ? diffRes : 0
        } else if (selCoupon.discount > 0) {
          this.payMoneyThis = this.payMoney * selCoupon.discount / 100
        }
      } else {
        this.payMoneyThis = this.payMoney
      }
      // 随市集币选择变化
      if (this.marketMoney.isUse) {
        if (this.payMoneyThis < this.marketMoney.amount / this.marketMoney.moneyToCoinRate) {
          this.marketMoney.usableAmount = this.payMoneyThis * this.marketMoney.moneyToCoinRate
          this.payMoneyThis = 0
        } else {
          let diffRes1 = this.payMoneyThis - this.marketMoney.amount / this.marketMoney.moneyToCoinRate
          this.payMoneyThis = diffRes1 >= 0 ? diffRes1 : 0
          this.marketMoney.usableAmount = this.marketMoney.amount
        }
      }
      this.payMoneyThis = this.payMoneyThis.toFixed(2) * 1
      this.marketMoney.usableAmount = this.marketMoney.usableAmount.toFixed(0) * 1
    },
    /** 支付方式-相关方法*********start*******/
    // 支付方式选择
    payWayOnSelect (item) {
      this.payWay.selType = item.code
      this.payWay.show = false
    },
    // 支付方式取消
    payWayOnCancel () {
      this.payWay.selType = ''
    },
    /** 支付相关方法***/
    // 点击支付
    clickPay () {
      this.pay().then(res => {
        if (res === 1) {
          this.isShow = false
        }
        this.$emit('payResult', res)
      })
    },
    pay () {
      return new Promise((resolve, reject) => {
        let subData = {
          orderId: this.orderId,
          couponId: this.selCoupon ? this.selCoupon.id : 0,
          marketCoin: this.marketMoney.isUse ? this.marketMoney.usableAmount : 0,
          payType: (this.payWay.selType === '1' ? 'BALANCE' : this.payWay.selType === '2' ? 'WXPAY' : '') }
        if (subData.payType === '') {
          this.$toast.fail('请选择支付方式')
        } else if (subData.payType === 'BALANCE') {
          this.$dialog.confirm({
            title: '确认购买',
            message: '是否使用余额购买?'
          }).then(() => {
            this.$post(this.payUrl, subData).then(res => {
              if (res.code === 'SUCCESS') {
                resolve(1)
              }
            })
          }).catch(() => {
            resolve(-1)
          })
        } else if (subData.payType === 'WXPAY') {
          this.startWxPay(this.payUrl, subData).then(res => {
            if (res === 1) {
              this.$toast.success({message: '支付成功', duration: 2000})
            } else if (res === -1) {
              this.$toast.success({message: '支付失败', duration: 2000})
            } else if (res === 0) {
              this.$toast.success({message: '已取消支付', duration: 2000})
            } else {
            }
            resolve(res)
          })
        }
      })
    }
  },
  watch: {
    show (val) {
      this.isShow = val
    },
    isShow (val) {
      if (val) {
        this.init()
      } else {
        // Object.assign(this.$data, this.$options.data())
        this.$emit('update:show', false)
      }
    },
    'coupon.chosenCoupon': {
      handler (nv, ov) {
        this.calcPayMoneyThis()
      },
      immediate: false
    },
    'marketMoney.isUse': {
      handler (val, oval) {
        this.calcPayMoneyThis()
      },
      immediate: true
    },
    payMoney: {
      handler (val, oval) {
        this.payMoneyThis = val
      },
      immediate: true
    },
    payMoneyThis: {
      handler (nv, ov) {
        this.judgeBalanceByPayMeonyThis(nv)
      },
      immediate: true
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .align-center{
    display: flex;
    align-items: center;
  }
  .level-center{
    display: flex;
    justify-content: center;
  }
  .popup-pay-index{
    font-family: Arial, "Microsoft Yahei", "Helvetica Neue", Helvetica, sans-serif !important;
    .head{
      position: relative;
      justify-content: space-between;
      height: 80px;
      border-bottom: 1px solid #c8c9ccfc;
      .title{
        font-size: 24px;
        font-weight: 600;
        position: absolute;
        top: 50%;left: 50%;
        transform: translate(-50%, -50%);
      }
      .close-icon{
        margin-right: 18px;
      }
    }
    .content{
      .item{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 80px;
        border-bottom: 1px solid #c8c9ccfc;
        padding: 0 18px;
        div:first-child{
        }
      }
      .coupon div:last-child,.pay-way div:last-child{
        color: #3333339e!important;
      }
      .market-money{
        strong{
          font-weight: bold;
        }
      }
      .wx-pay:active,.balance:active,.pay-way:active{
        opacity: .5;
      }
      .pay-money div:last-child{
        font-size: 38px;
        font-weight: 600;
        color: #333333a1;
      }
    }
    .bottom{
      height: 140px;
      /deep/ .van-button {
        width: 90%;
        color: #d8b96f;
        background-color: rgb(0, 0, 0);
        border-radius: 10px;
      }
    }
  }
</style>



