import base from './base.js'
import globalParam from '@/globalParam.js'
export default {
  data () {
    return {
    }
  },
  mixins: [base],
  computed: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    /** *修改分享信息配置*****/
    // 低版本微信不支持wx.updateAppMessageShareData方法，改用其他即将弃用的配置方法   -2018/12/24
    updateAppMessageShareData (shareData) {
      let self = this
      let link = globalParam.domainName + shareData.link
      self.wx.ready(function () { // 需在用户可能点击分享按钮前就先调用
        self.wx.onMenuShareAppMessage({
          title: shareData.title, // 分享标题
          desc: shareData.desc, // 分享描述
          link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: shareData.imgUrl, // 分享图标
          success: function () {
            // 设置成功
          }
        })
        self.wx.onMenuShareTimeline({
          title: shareData.title, // 分享标题
          link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: shareData.imgUrl, // 分享图标
          success: function () {
            // 用户点击了分享后执行的回调函数
          }
        })
        self.wx.updateTimelineShareData({
          title: shareData.title, // 分享标题
          link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: shareData.imgUrl, // 分享图标
          success: function () {
            // 用户点击了分享后执行的回调函数
          }
        })
        self.wx.updateAppMessageShareData({
          title: shareData.title, // 分享标题
          desc: shareData.desc, // 分享描述
          link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
          imgUrl: shareData.imgUrl, // 分享图标
          success: function () {
            // 设置成功
          }
        })
      })
    },
    /** *分享相关的微信jssdk授权*****/
    initShareWxConfig () {
      return new Promise((resolve, reject) => {
        this.weChatConfig(this.getCurrentUrl(), ['updateAppMessageShareData', 'onMenuShareAppMessage', 'showMenuItems', 'showAllNonBaseMenuItem',
          'onMenuShareTimeline', 'updateTimelineShareData', 'hideMenuItems', 'hideAllNonBaseMenuItem']).then(() => {
          resolve()
        })
      })
    }
  }
}



