import base from './base.js'
export default {
  data () {
    return {
    }
  },
  mixins: [base],
  computed: {
  },
  created () {
  },
  mounted () {
  },
  methods: {
    /** ***调用微信支付*****/
    wxPay (payUrl, subData) {
      return new Promise((resolve, reject) => {
        this.$post(payUrl, subData).then(res => {
          var self = this
          self.wx.chooseWXPay({
            timestamp: res.data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
            nonceStr: res.data.nonceStr, // 支付签名随机串，不长于 32 位
            package: res.data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
            signType: res.data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
            paySign: res.data.paySign, // 支付签名
            success: function (resSuc) { // 支付成功后的回调函数
              let wxpaynoticeparam = { attach: res.data.attach, out_trade_no: res.data.out_trade_no }
              self.wxPaySuccessed(wxpaynoticeparam).then(res => {
                resolve(res)
              }).catch(err => {
                reject(err)
              })
            },
            fail: function (resFail) {
            },
            cancel: function () {
              resolve(0)
            }
          })
        }).catch(err => {
          reject(err)
        })
      })
    },
    /** ***微信支付成功后的回调*****/
    wxPaySuccessed (data) {
      return new Promise((resolve, reject) => {
        let paramObj = { attach: data.attach, out_trade_no: data.out_trade_no }
        let paramXml = `<xml>${this.$x2js.js2xml(paramObj)}</xml>`
        this.$post(this.$api.wxpayweixinnotify, paramXml).then(res => {
          let resObj = this.$x2js.xml2js(res)
          if (resObj.xml.return_code === 'SUCCESS') {
            resolve(1)
          } else {
            resolve(-1)
          }
        }).catch(err => {
          reject(err)
        })
      })
    },
    startWxPay (payUrl, data) {
      return new Promise((resolve, reject) => {
        this.weChatConfig(this.getCurrentUrl(), ['chooseWXPay']).then(() => {
          this.wxPay(payUrl, data).then(res => {
            resolve(res)
          }).catch(err => {
            reject(err)
          })
        })
      })
    }
  }
}



